<template>
  <div class="book">
    <Title-Bar :ifTitleAndMenuShow="ifTitleAndMenuShow"></Title-Bar>
    <div class="read-wrapper">
      <div id="read" 
      :style="{background: themeColor}"
      ></div>
      <div class="mask">
          <div class="mask__left" @click="lastPage"></div>
          <div class="mask__center" @click="toggleTitleAndMenu"></div>
          <div class="mask__right" @click="nextPage"></div>
      </div>
    </div> 
    <Menu-Bar :ifTitleAndMenuShow="ifTitleAndMenuShow" 
    :fontSizeList="fontSizeList"
    :defaultFontSize="defaultFontSize"
    @setFontSize="setFontSize"
    :themeList="themeList"
    :defaultTheme="defaultTheme"
    @setTheme="setTheme"
    ref="menuBar"
    :bookAvailable="bookAvailable"
    :progress="progress"
    @onProgressChange="onProgressChange"
    @jumpTo="jumpTo"
    :navigation="navigation"
    ></Menu-Bar>
  </div>
</template>

<script>
import Epub from 'epubjs'
import TitleBar from './components/TitleBar.vue'
import MenuBar from './components/MenuBar.vue'
const DOWNLOAD_URL = './pg1342-images.epub'

export default {
    data(){
        return {
            ifTitleAndMenuShow: false,
            fontSizeList: [
                {fontSize: 12},
                {fontSize: 14},
                {fontSize: 16},
                {fontSize: 18},
                {fontSize: 20},
                {fontSize: 22},
                {fontSize: 24}
            ],
            defaultFontSize: 16,
            defaultTheme: 0, 
            themeList: [
                {name: 'default', 
                style: {
                    body: {
                        'color': '#000',
                        'background': '#fff'
                        }
                    }
                },
                {name: 'eye', 
                style: {
                    body: {
                        'color': '#000',
                        'background': '#ceeaba'
                        }
                    }
                },
                 {name: 'night', 
                style: {
                    body: {
                        'color': '#fff',
                        'background': '#121212'
                        }
                    }
                },
                {name: 'gold', 
                style: {
                    body: {
                        'color': '#000',
                        'background': 'rgb(241,236,226)'
                        }
                    }
                }
            ],
            bookAvailable: false,
            progress:0,
            navigation: null
        }
    },
    computed: {
        //用来修复换章节时出现的闪烁
        themeColor(){
            return this.themeList[this.defaultTheme].style.body.background;
        }
    },
    methods: {
        //生成电子书
        showEpub(){
            this.book = new Epub(DOWNLOAD_URL);
            this.rendition = this.book.renderTo('read', 
              {method: "continuous", width: window.innerWidth, height: window.innerHeight,
              resizeOnOrientationChange: true}
                
            );
            this.rendition.display();
            this.themes = this.rendition.themes; 
            this.themes.fontSize(this.defaultFontSize + 'px');
            this.registerTheme();
            this.setTheme(this.defaultTheme);
            this.book.ready.then(() => {
                this.navigation = this.book.navigation;
                return this.book.locations.generate()
            }).then(() => {
                this.locations = this.book.locations;
                this.bookAvailable = true;
            })
         

        },
        //翻页
        lastPage(){
            if (this.rendition){
                this.rendition.prev();
            }
        },
        nextPage(){
             if (this.rendition){
                this.rendition.next();
            }
        },
        //菜单的显示和隐藏
        toggleTitleAndMenu(){
            this.ifTitleAndMenuShow = !this.ifTitleAndMenuShow;
            if (!this.ifTitleAndMenuShow){
                this.$refs.menuBar.hideSetting();
            }            
        },
        hideTitleAndMenu(){
            this.ifTitleAndMenuShow = false;
            this.$refs.menuBar.hideSetting();
            this.$refs.menuBar.hideContents();

        },
        //设置字号
        setFontSize(fontSize){
            this.defaultFontSize = fontSize;
            if (this.themes) {
                  this.themes.fontSize(this.defaultFontSize + 'px');
            }
          
        },
        //设置主题
        registerTheme(){
             this.themeList.forEach(theme => {
                this.themes.register(theme.name, theme.style);
            });
        },
        setTheme(index){
            console.log(index);
            this.defaultTheme = index;
            this.themes.select(this.themeList[index].name);
        },
        //跳转
        onProgressChange(progress){
            const percentage = progress / 100;
            const location = percentage > 0 ?  
            this.locations.cfiFromPercentage(percentage): 0
            this.rendition.display(location)
            this.progress = parseInt(progress);
        },
        jumpTo(link){
            this.rendition.display(link);
            this.hideTitleAndMenu();
        }

       
    },
    mounted(){
        this.showEpub();
        //电子书宽高随窗口变化
        window.addEventListener('resize', () => {
            this.rendition.resize(window.innerWidth, window.innerHeight)
        });
        //左右箭头翻页
        document.onkeydown = (e) => {
            if (e.keyCode === 37){
                this.lastPage();
            } else if (e.keyCode === 39){
                this.nextPage();
            }
        }
        
      
    },
    components: {
        TitleBar, MenuBar
    }
}
</script>

<style lang='scss' scoped>

@import 'assets/styles/global';

.read-wrapper {
    position: relative;
    .mask {
        position: absolute;
        z-index: 100;
        left:0;
        top: 0;
        display: flex;
        width: 100%;
        height: 100%;
   

        &__left, &__right {
            flex: 0 0 px2rem(100);
           
        }

        &__center {
            flex: 1;
        }
    }
}

#read {
    width: 100%;
}








</style>