<template>
    
        <div class="contents">
            <div class="contents-wrapper" v-if="bookAvailable">
                <div class="contents-item" :key="index"
                v-for="(item, index) in navigation.toc"
                @click="jumpTo(item.href)">
                    <span class="text">
                        {{item.label}}
                    </span>
                </div>
            </div>
            <div class="empty" v-else>
                加载中...
            </div>
        </div>


</template>

<script>
export default {
    props: {
   
        navigation: Object, 
        bookAvailable: Boolean
    },
    methods: {
        jumpTo(target) {
            this.$emit('jumpTo', target)
        }
    }
}
</script>

<style lang="scss" scoped>
 @import '../assets/styles/global';
    .contents {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 103;
        background: #fff;
        font-size: px2rem(14);
        width: fit-content;
        height: 100%;
        box-shadow: px2rem(6) 0 px2rem(6) rgba(#000, .15);
        box-sizing: border-box;
        padding: 0 px2rem(10);
        overflow-y: scroll;
        .contents-wrapper {
            .contents-item {
            width: fit-content;
            padding: px2rem(12);
            cursor: pointer;
            text-decoration: underline;
            }
        }
       

    }

    .empty {
        @include center;
        width: 100%;
        height: 100%;
    }
</style>