<template>
    <div class="menu-bar">
        <transition name="slide-up">
            <div class="setting-wrapper" v-show="ifSettingShow" ref="setting">
                <div class="setting-font-size" v-if="showTag == 0">
                    <div class="preview" 
                    :style="{fontSize: fontSizeList[0].fontSize + 'px', transform: previewTranslateRight}" 
                    ref="preview">A</div>
                    <div ref="select" class="select">
                        <div class="select-wrapper" :key="index" 
                        v-for="(item, index) in fontSizeList"  @click="setFontSize(item.fontSize)">
                            <div class="line"></div>
                            <div class="point-wrapper">
                                <div class="point" v-show="item.fontSize === defaultFontSize">
                                    <div class="point-small">

                                    </div>
                                </div>
                            </div>
                            <div class="line"></div>
                        </div>
                    </div>

                    <div class="preview" 
                        :style="{fontSize: fontSizeList[fontSizeList.length-1].fontSize + 'px', transform: previewTranslateLeft}">A
                    </div>
                </div>

                <div class="setting-theme" v-else-if="showTag==1">
                    <div class="setting-theme-item" :key="index" 
                    v-for="(item, index) in themeList"
                    @click="setTheme(index)">
                        
                        <div class="preview" 
                        :style="{background: item.style.body.background}"
                        :class="{border: item.style.body.background === '#fff'}"></div>
                        <div class="text" :class="{selected: index === defaultTheme}">{{item.name}}</div>
                    </div>
                </div>

                <div class="setting-progress" v-else-if="showTag==2">
                    <div class="progress-wrapper">
                        <input class="progress" type="range" max="100" min="0" step="1"
                        @change="onProgressChange"
                        @input="onProgressChange"
                        :value="progress"
                        :disabled="!bookAvailable"
                        ref="progress">
                        <span class="text">
                            {{bookAvailable ? progress + '%' : '加载中...'}}
                        </span>
                    </div>
                 
                </div>
            </div>
        </transition>
       
        <transition name="slide-up">
            <div class="menu-wrapper"  v-show="ifTitleAndMenuShow" :class="{'hide-box-shadow': ifSettingShow || !ifTitleAndMenuShow}">
                <div class="icon-wrapper" @click="showSetting(3)">
                    <span class="icon-menu icon"></span>
                </div>
                <div class="icon-wrapper" @click="showSetting(2)">
                    <span class="icon-progress icon"></span>
                </div>
                <div class="icon-wrapper"  @click="showSetting(1)">
                    <span class="icon-bright icon"></span>
                </div>
                <div class="icon-wrapper" @click="showSetting(0)">
                    <span class="icon-a icon" >A</span>
                </div>
            </div>
        </transition>

        <transition name="slide-right">
            <contents-view 
            v-show="ifContentsShow"
            :navigation="navigation"
            :bookAvailable="bookAvailable"
            @jumpTo="jumpTo"></contents-view>
        </transition>
        <transition name="fade">
            <div class="contents-mask"
                 v-show="ifContentsShow"
                 @click="hideContents">

            </div>
        </transition>
        
    </div>
 

</template>

<script>

import contentsView from './contents.vue'

export default {
    props: {
        ifTitleAndMenuShow: {
            type: Boolean,
            default: false
        },
        fontSizeList: {
            type: Array
        },
        defaultFontSize: Number,
        themeList: Array,
        defaultTheme: Number,
        bookAvailable: Boolean,
        progress: Number,
        navigation: Object
    },
    data(){
        return {
            ifSettingShow: false,
            previewTranslateLeft: undefined,
            previewTranslateRight: undefined,
            showTag: 0,
            ifContentsShow: false
        }
    },
    methods: {
        showSetting(tag){
          
      
            this.showTag = tag;
            if (this.showTag === 3) {
                this.ifContentsShow = true;
                this.ifSettingShow = false;
            } else {
                this.ifContentsShow = false;
                this.ifSettingShow = true;
            }
        },
        hideSetting(){
            this.ifSettingShow = false;
        },
        hideContents(){
            this.ifContentsShow = false;
        },
        centerFontSettingPreview(){
             //避免用户看到setting, 设置样式使它远离底部 
            this.$refs.setting.setAttribute('id', 'moveAway');
            //为了获取line的宽度，要暂时显示setting
            this.ifSettingShow = true;
            let width;
            //等待进入动画完成 
            setTimeout(() => {
                width = this.$refs.select.querySelector('.line').getBoundingClientRect().width;
                this.ifSettingShow = false;
                this.previewTranslateLeft = `translateX(${-width/2}px)`;
                this.previewTranslateRight = `translateX(${width/2}px)`;
                setTimeout(() => {
                this.$refs.setting.removeAttribute('id');
                }, 600)
            

            }, 500)

        },
        setFontSize(fontSize){
            this.$emit('setFontSize', fontSize);
        },
        setTheme(index){
          
            this.$emit('setTheme', index)
        },
        onProgressChange(e){
            this.$emit('onProgressChange', e.target.value);
            this.$refs.progress.style.backgroundSize = `${this.progress}% 100%`
        },
        jumpTo(link){
            this.$emit('jumpTo', link)
        }
    },
    mounted(){
        this.centerFontSettingPreview(); 
    },
    components: {
        contentsView
    }


   
}
</script>

<style lang="scss" scoped>
@import '../assets/styles/global';
 #moveAway {
        position: absolute;
        bottom: -1000px;
    }


.menu-bar {
    .menu-wrapper {
        position: absolute;
        left: 0;
        bottom: 0;
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 100%;
        height: px2rem(48);
        background: #fff;
        box-shadow: 0 px2rem(-6) px2rem(8) rgba(#000, .15);
        z-index: 101;
        .icon-wrapper {
            cursor: pointer;
            @include center;
            .icon-bright{

                font-size: px2rem(24);
            }
            
            .icon-progress {
                font-size: px2rem(26);
            }
        }
        &.hide-box-shadow {
            box-shadow: unset;
        }
    }
    .setting-wrapper{
        z-index: 101;
        position: absolute;
        bottom: px2rem(48);
        height: px2rem(60);
        width: 100%;
        background: #fff;
        box-shadow: 0 px2rem(-6) px2rem(8) rgba(#000, .15);
    }

    .setting-font-size {
        display: flex;
        height: 100%;
        position: relative;
        .preview {
            flex: 0 0 px2rem(40);
            @include center;   
        }

        .select {
            display: flex;
            flex: 1;

            .select-wrapper {
                display: flex;
                align-items: center;
                flex: auto;
                &:first-child {
                    .line:first-child {
                        border-top: none;
                  
                    }
                   
                }

                &:last-child {
                    .line:last-child {
                        border-top: none;
                    }
                  
                }


         
            .line {
                flex: 1;
                height: 0;
                border-top: px2rem(1) solid #ccc;

            }

            .point-wrapper {
                flex: 0 0 0;
                width: 0;
                height: px2rem(7);
                border-left: px2rem(1) solid #ccc;
                position: relative;
                .point {
                    position: absolute;
                    left: px2rem(-6);
                    top: px2rem(-4);
                    width: px2rem(20);
                    height: px2rem(20);
                    background: #fff;
                    border-radius: 50%;
                    border: 1px solid #ccc;
                    box-shadow: 0 px2rem(2) px2rem(2) rgba(#000, .15);
                    @include center;
                }
                
                .point-small {
                    position: absolute;
                    width: px2rem(6);
                    height: px2rem(6);
                    background: #000;
                    border-radius: 50%; 
                }
            }


            }
        }
        
    }

    .setting-theme {
        display: flex;
        height: 100%;
        .setting-theme-item {
            cursor:pointer;
            flex: 1;
            display: flex;
            flex-direction: column;
            padding: px2rem(20) px2rem(10) 0 px2rem(10);
            box-sizing: border-box;
        }

        .preview {
            
            flex: 1 1 auto;
            box-sizing: border-box;
            &.border {
                border: 1px solid #ccc;
            }
        }

        .text {
            @include center;
            font-size: px2rem(14);
            line-height: 1.5;
            color: #ccc;
            &.selected {
                color: #333;
            }
        }
    }
    .setting-progress {
        width: 100%;
        height: 100%;
        @include center;
        .progress-wrapper {
            width: 100%;
            display: flex;
            flex-direction: column;
            padding: px2rem(20) px2rem(30) 0 px2rem(30);
            
        }

        .progress {
            -webkit-appearance: none;
            height: px2rem(2);
            width: 100%;
            background: linear-gradient(#999, #999) no-repeat, #ddd;
            background-size: 0 100%;

            &:focus {
                outline: none;
            }

            &::-webkit-slider-thumb {
                -webkit-appearance: none;
                height: px2rem(20);
                width: px2rem(20);
                border-radius: 50%;
                background: #fff;
                box-shadow: 0 4px 4px 0 rgba(#000, .15);
                border: px2rem(1) solid #ddd;
            }
        }

        .text {
            @include center;
            font-size: px2rem(18);
            padding-top: px2rem(12);
        }

    }

    .contents-mask {
        position:absolute;
        top: 0;
        left: 0;
        z-index: 101;
        display: flex;
        width: 100%;
        height: 100%;
        background: rgba(51,51,51, .8);
    }
}
   
</style>